@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
.App {
  font-family: sans-serif;
  text-align: center;
  margin: 0;
  padding: 0;
}

* {
  margin: 0;
  padding: 0;
  font-family: "Roboto", sans-serif;
}

body {
  background: #eee7e7;
}

.nav {
  height: 70px;
  background: #fb5b16;
  color: #fffbfb;
  display: flex;
  align-items: center;
  padding-left: 20px;
  font-size: 30px;
  box-shadow: 0px 4px 4px 0px #00000040;
}

.main {
  display: flex;
}
.section-1 {
  width: 35vw;
  padding: 80px;
  text-align: center;
  line-height: 3;
}
.head {
  color: #fb5b16;
}
.btn {
  padding: 10px;
  border: 0;
  width: 30%;
  border-radius: 15px;
  background-color: #fb5b16;
  color: white;
}
.para {
  padding-bottom: 50px;
}
.left {
  text-align: left;
}

@media only screen and (max-width: 1150px) {
  .main {
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  .left {
    text-align: center;
  }
  .section-1 {
    width: 100vw;
  }
  .section-2 {
    width: 80vw;
  }
}

.skills {
  display: flex;
  justify-content: center;
  padding: 30px;
}

.pad {
  text-align: center;
  justify-content: center;
  padding: 30px;
  font-weight: bold;
}

.pad p {
  padding-top: 10px;
}

.skill {
  text-align: center;
}

.orange {
  color: #fb5b16;
}

@media only screen and (max-width: 860px) {
  .skills {
    display: grid;
    grid-template-columns: auto auto;
  }
}

.work {
  text-align: center;
  color: #373365;
  padding-bottom: 40px;
  padding-top: 40px;
}
.flx {
  display: flex;
}

.col {
  display: flex;
}
.circle {
  width: 30px;
  height: 30px;
  border-radius: 50%;
  background: #590bb2;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #fff;
  font-weight: bold;
  margin-right: 30px;
}
.book {
  color: #373365;
  padding-bottom: 25px;
}
.pad-right {
  margin-right: 50px;
}

.pad-img {
  margin-left: 50px;
}

.banner-2 {
  padding-top: 30px;
  padding-bottom: 40px;
  display: flex;
  justify-content: center;
  margin-bottom: 30px;
}

@media only screen and (max-width: 1200px) {
  .flx {
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  .img {
    width: 80vw;
    padding-bottom: 30px;
  }
  .circle {
    margin-left: 30px;
  }
}
@media only screen and (max-width: 570px) {
  .banner-img {
    width: 80vw;
  }
}
/*banner-img*/

.code {
  background-color: #fff;
}
.heading {
  text-align: center;
  padding: 30px;
  padding-bottom: 40px;
}
.heading h2 {
  color: #fb5b16;
}
.sec {
  display: flex;
  justify-content: center;
  align-items: center;
}

.logo {
  display: flex;
  padding-bottom: 30px;
}
.sec2 {
  margin-right: 25px;
}
.logo-sec {
  margin-right: 20px;
}

.sec3 {
  margin-left: 30px;
}

.btn2 {
  border-radius: 15px;
  border: 0;
  background-color: #fb5b16;
  padding: 10px;
  color: #fff;
}
.buttn {
  display: flex;
  justify-content: center;
  padding-top: 35px;
  padding-bottom: 50px;
}

@media only screen and (max-width: 1015px) {
  .sec2 {
    grid-area: first;
  }
  .secim {
    grid-area: image;
    text-align: center;
  }
  .sec3 {
    grid-area: second;
  }
  .sec {
    display: grid;
    grid-template-areas:
      "image image"
      "first second";

    grid-gap: 20px;
  }
}

@media only screen and (max-width: 750px) {
  .sec2 {
    padding-left: 25px;
  }
  .sec3 {
    padding-right: 25px;
  }
}

.box {
  display: flex;
  justify-content: center;
}
.back {
  width: 20vw;
  background-color: #fff;
  text-align: center;
  margin: 20px;
}
.cir-flex {
  padding-top: 20px;
  display: flex;
  justify-content: center;
  padding-bottom: 20px;
}
.cir {
  width: 15px;
  height: 15px;
  margin: 6px;
  border-radius: 50%;
  background: #e1c5cf; /* #7e57c2 */
}
.active {
  background: #7e57c2;
}
.cir:hover {
  background: #7e57c2;
  cursor: pointer;
}
.banner1 {
  display: flex;
  justify-content: center;
}

.logo-image {
  width: 100%;
}

a {
  text-decoration: none;
  font-weight: bold;
}

@media only screen and (max-width: 850px) {
  .div1 {
    grid-area: fir;
  }
  .div2 {
    grid-area: sec;
  }
  .div3 {
    grid-area: th;
  }
  .div4 {
    grid-area: for;
  }
  .box {
    display: grid;
    grid-template-areas:
      "fir sec"
      "th for";
    text-align: center;
  }
  .back {
    width: 40vw;
  }
}

@media only screen and (max-width: 745px) {
  .ban {
    width: 80vw;
  }
}

.learn {
  margin-top: 20px;
  padding: 40px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  color: #590bb2;
  background: #eae7fb;
}

.timeline-img {
  width: 95%;
}

